import "./navbar.scss"
import React from "react"
import logo from "./logo.png"
import { Outlet, Link } from 'react-router-dom'
import { useState } from "react";
import mode from "./modee.png"
// import menu from "./menu.png"


function Navbar({ changeIt, light,menu,source }) {

    const openNav = () => {
        var width = window.innerWidth;
        if (width <= 900 && width > 800) {
            document.getElementById("myNav").style.width = "35%";
        }
        else if (width <= 800 && width > 700) {
            document.getElementById("myNav").style.width = "40%";
        }
        else if (width <= 700 && width > 600) {
            document.getElementById("myNav").style.width = "45%";
        }
        else if (width <= 600 && width > 500) {
            document.getElementById("myNav").style.width = "50%";
        }
        else if (width <= 500 && width > 400) {
            document.getElementById("myNav").style.width = "62%";
        }
        else if (width <= 400 && width > 200) {
            document.getElementById("myNav").style.width = "75%";
        }
        else if (width <= 200 && width > 0) {
            document.getElementById("myNav").style.width = "100%";
        }
    }
    const closeNav = () => {
        document.getElementById("myNav").style.width = "0%";
    }
    // const [aboutContent, setAboutContent] = useState('About');
    // const [blogContent, setBlogContent] = useState('Blogs');
    // const [projectsContent, setProjectsContent] = useState('Projects');
    // const [skillsContent, setSkillsContent] = useState('Skills');
    // const [contactContent, setContactContent] = useState('Contact');

    // const aboutMouseEnter = () => {
    //     setAboutContent('');
    //   };

    //   const aboutMouseLeave = () => {
    //     setAboutContent('About');
    //   }

    //   const blogMouseEnter = () => {
    //     setBlogContent('');
    //   };

    //   const blogMouseLeave = () => {
    //     setBlogContent('Blogs');
    //   }

    //   const projectsMouseEnter = () => {
    //     setProjectsContent('');
    //   };

    //   const projectsMouseLeave = () => {
    //     setProjectsContent('Projects');
    //   }

    //   const skillsMouseEnter = () => {
    //     setSkillsContent('');
    //   };

    //   const skillsMouseLeave = () => {
    //     setSkillsContent('Skills');
    //   }

    //   const contactMouseEnter = () => {
    //     setContactContent('');
    //   };

    //   const contactMouseLeave = () => {
    //     setContactContent('Contact');
    //   }


    return (
        <div className="Navbar" >
            <div className={light == "light" ? "nav-container nav-container-light" : "nav-container"}>
                <Link className="link-nav" to="/">
                    <div className="left-nav">

                        <img className="logo" src={logo} />
                        <h1 className={light == "light" ? "name name-light" : "name"} >Sahil Kalkal</h1>

                    </div>
                </Link>
                <div className="right-nav">
                    <div className="menu">
                        <Link className="link-nav" to="/about">
                            <p id="myButton" className={light == "light" ? "menuItem menuItem-light" : "menuItem"}  >About</p>
                        </Link>
                        <Link className="link-nav" to="/blogs">
                            <p id="blog-nav" className={light == "light" ? "menuItem menuItem-light" : "menuItem"}  >Blogs</p>
                        </Link>
                        <Link className="link-nav" to="/projects">
                            <p id="projects-nav" className={light == "light" ? "menuItem menuItem-light" : "menuItem"} >Projects</p>
                        </Link>
                        <Link className="link-nav" to="/skills">
                            <p id="skills-nav" className={light == "light" ? "menuItem menuItem-light" : "menuItem"}  >Skills</p>
                        </Link>
                        {/* <Link className="link-nav" to="/blogs">
                                <p className={light ? "menuItem menuItem-light" : "menuItem"} >Blogs</p>
                            </Link> */}
                        <Link className="link-nav" to="/contact">
                            <p id="contact-nav" className={light == "light" ? "menuItem menuItem-light" : "menuItem"} >Contact</p>
                        </Link>
                        {/* <Link className="link-nav" to="/newsletter">
                                <p className={light ? "menuItem menuItem-light" : "menuItem"} >Newsletter</p>
                            </Link> */}
                        <img className="mode" onClick={changeIt} src={source} />
                        <img onClick={openNav} alt="menu-bar" className="menu-icon" src={menu} />





                    </div>
                </div>
            </div>

            <div id="myNav" className="overlay">
                <p className="closebtn" onClick={closeNav}>&times;</p>
                <div className="overlay-content">



                    <p onClick={closeNav}  ><Link to="/about" className="mt" >About</Link></p>
                    <p onClick={closeNav}  ><Link to="/blogs" className="mt" >Blogs</Link></p>
                    <p onClick={closeNav}  ><Link to="/projects" className="mt" >Projects</Link></p>
                    <p onClick={closeNav}  ><Link to="/skills" className="mt" >Skills</Link></p>
                    <p onClick={closeNav}  ><Link to="/contact" className="mt" >Contact</Link></p>
                    {/* <p onClick={closeNav}  ><Link to="/newsletter" className="mt" >Newsletter</Link></p> */}



                </div>
            </div>
        </div>
    )
}




export default Navbar;