import "./skills.scss"
import html from "./html.png"
import css from "./css.png"
import js from "./js.png"
import react from "./react.png"
import c from "./c.png"
import cplus from "./c++.png"
import sql from "./sql.png"
import fb from "./firebase.png"
import figma from "./figma.png"
import github from "./github.png"
import git from "./git.png"
import npm from "./npm.png"
import vs from "./vs.png"
import ScrollToTop from "react-scroll-to-top"

function Skills({ light }) {


    const skills = [
        {
            "name": "HTML",
            "src": html,
            "id" : "html",
            "proficiency": "Expert"
        },
        {
            "name": "CSS",
            "src": css,
            "id" : "css",
            "proficiency": "Intermediate"
        },
        {
            "name": "JS",
            "src": js,
            "id" : "js",
            "proficiency": "Intermediate"
        },
        {
            "name": "react.js",
            "src": react,
            "id" : "react",
            "proficiency": "Intermediate"
        },
        {
            "name": "C",
            "src": c,
            "id" : "c",
            "proficiency": "Expert"
        },
        {
            "name": "C++",
            "src": cplus,
            "id" : "cplus",
            "proficiency": "Intermediate"
        },
        {
            "name": "SQL",
            "src": sql,
            "id" : "sql",
            "proficiency": "Intermediate"
        },
    ]

    const tools = [
        {
            "name": "Firebase",
            "src": fb,
            "id" : "fb",
            "proficiency": "Expert"
        },
        {
            "name": "Figma",
            "src": figma,
            "id" : "figma",
            "proficiency": "Intermediate"
        },
        {
            "name": "GitHub",
            "src": github,
            "id" : "github",
            "proficiency": "Intermediate"
        },
        {
            "name": "Git",
            "src": git,
            "id" : "git",
            "proficiency": "Intermediate"
        },
        {
            "name": "npm",
            "src": npm,
            "id" : "npm",
            "proficiency": "Expert"
        },
        {
            "name": "VS Code",
            "src": vs,
            "id": "vs-code",
            "proficiency": "Intermediate"
        },

    ]

    return (
        <div className="Skills">
            <p className={light == "light" ? "headingg headingg-light" : "headingg"} >Skills</p>
            <div className="skills-container">
                {
                    skills.map((sk) => (
                        <div className={light == "light" ? "skill skill-light" : "skill"} id={sk.id} >
                            <img className="skill-img" alt="skill image" src={sk.src} />
                            <p className={light == "light" ? "skill-name skill-name-light" : "skill-name"}>{sk.name}</p>
                            {/* <p className="proficiency">{sk.proficiency}</p> */}
                        </div>
                    ))
                }
            </div>
            <br /><br /><br /><br />
            <p className={light == "light" ? "headingg headingg-light" : "headingg"} >Tools</p>
            <div className="skills-container">
                {
                    tools.map((sk) => (
                        <div className={light == "light" ? "skill skill-light" : "skill"} id={sk.id}>
                            <img className="skill-img" alt="tool image" src={sk.src} />
                            <p className={light == "light" ? "skill-name skill-name-light" : "skill-name"}  >{sk.name}</p>
                            {/* <p className="proficiency">{sk.proficiency}</p> */}
                        </div>
                    ))
                }
            </div>
            <ScrollToTop smooth />
        </div>
    )
}
export default Skills;